import axios from 'axios';

import * as actionTypes from './actionTypes';



export const authStart=()=>{
    return{
        type: actionTypes.AUTH_START
    }
};

export const authSuccess=(token,userId)=>{
    return{
        type: actionTypes.AUTH_SUCCESS,
        token,
        userId
    }
};

export const authFailed=(error)=>{
    return{
        type: actionTypes.AUTH_FAILED,
        error
    }
};

export const authLogout=()=>{
    localStorage.removeItem('token');
    localStorage.removeItem('expirationDate');
    localStorage.removeItem('userId');
    return{
        type: actionTypes.AUTH_LOGOUT
    }
};

export const checkAutTimeOut=(expiresIn)=>{
    return dispatch =>{
        setTimeout(()=>{
            console.log(expiresIn);
            dispatch(authLogout())
        },expiresIn*1000)
    }
};

export const auth=(email,password,isSignup)=>{
    return dispatch => {
        dispatch(authStart());
        const authData = {
            email:email,
            password:password,
            returnSecureToken: true
        };
        let url = 'https://www.googleapis.com/identitytoolkit/v3/relyingparty/signupNewUser?key=AIzaSyDgQE2eCX5Pas3Kgu74xOcTcBlBwngdnpc';
        if(!isSignup){
            url = 'https://www.googleapis.com/identitytoolkit/v3/relyingparty/verifyPassword?key=AIzaSyDgQE2eCX5Pas3Kgu74xOcTcBlBwngdnpc';
        }
        axios.post(url,authData)
            .then(response =>{
                const expirationDate = new Date(new Date().getTime() + response.data.expiresIn * 1000);
                localStorage.setItem('token',response.data.idToken);
                localStorage.setItem('expirationDate',expirationDate);
                localStorage.setItem('userId',response.data.localId);
                dispatch(authSuccess(response.data.idToken,response.data.localId));
                dispatch(checkAutTimeOut(response.data.expiresIn))   ;
            })
            .catch(error =>{
                dispatch(authFailed(error.response.data.error))
            });
    }
};

export const authRedirectPath=(path)=>{
    return{
        type:actionTypes.REDIRECT_AUTH_PATH,
        path
    }
};

export const checkAuthState=()=>{
    return dispatch =>{
        const token = localStorage.getItem('token');
        if(!token){
            dispatch(authLogout());
        }
        const expirationDate = new Date(localStorage.getItem('expirationDate'));
        if(expirationDate <= new Date()){
            dispatch(authLogout());
        }else{
            dispatch(authSuccess(token,localStorage.getItem('userId')));
            dispatch(checkAutTimeOut((expirationDate.getTime() - new Date().getTime())/1000));
        }

    }
};









