import { AUTH_FAILED,AUTH_SUCCESS,AUTH_START,AUTH_LOGOUT,REDIRECT_AUTH_PATH} from '../actions/actionTypes';

const initialState = {
    token:null,
    userId:null,
    error:null,
    loading:false,
    authRedirectPath: '/'
};

const reducer=(state=initialState,action)=>{
    switch (action.type){
        case AUTH_START:
            return{...state,loading:true};
        case AUTH_SUCCESS:
            return{
                ...state,
                token:action.token,
                userId:action.userId,
                error:null,
                loading:false
            };
        case AUTH_FAILED:
            return{...state,error:action.error,loading:false};
        case AUTH_LOGOUT:
            return{...state,token:null,userId:null};
        case REDIRECT_AUTH_PATH:
            return{...state,authRedirectPath:action.path};
        default:
            return state;

    }
};

export default reducer;


