import { PURCHASE_INIT,PURCHASE_BURGER_FAILED,PURCHASE_BURGER_SUCCESS,PURCHASE_BURGER_START,FETCH_ORDERS_START,FETCH_ORDERS_FAILED,FETCH_ORDERS_SUCCESS} from '../actions/actionTypes';

const initialState = {
    orders:[],
    loading: false,
    purchase: false
};

const order=(state=initialState, action)=>{
    switch (action.type){
        case PURCHASE_INIT:
            return{
                ...state,
                purchase: false
            };
        case PURCHASE_BURGER_START:
            return{
                ...state,
                loading: true
            };
        case PURCHASE_BURGER_SUCCESS:
            const newOrder = {
                ...action.orderData,
                id: action.id
            };
            return{
                ...state,
                orders: state.orders.concat(newOrder),
                purchase: true,
                loading: false
            };
        case PURCHASE_BURGER_FAILED:
            return{
                ...state,
                loading: false
            };
        case FETCH_ORDERS_START:
            return{
                ...state,
                loading: true
            };
        case FETCH_ORDERS_SUCCESS:
            return{
                ...state,
                orders: action.orders,
                loading: false
            };
        case FETCH_ORDERS_FAILED:
            return{
                ...state,
                loading: false
            };
        default:
            return state;
    }
};

export default order;




