import React,{ Component } from 'react';
import { connect } from 'react-redux';
import { Redirect } from 'react-router-dom';
import * as actions from '../../store/actions';


import Input from "../../components/UI/Input/Input";
import Button from "../../components/UI/Button/Button";
import Spinner from '../../components/UI/Spinner/Spinner';
import classes from './Auth.css';




class Auth extends Component{
    state = {
        controls : {
            email:{
                elementType:'input',
                elementConfig:{
                    type:'email',
                    placeholder:'Email Address'
                },
                value:'',
                validation:{
                    isRequired: true,
                    isEmail: true
                },
                valid: false,
                touched: false
            },
            password:{
                elementType:'input',
                elementConfig:{
                    type:'password',
                    placeholder:'Password'
                },
                value:'',
                validation:{
                    isRequired: true,
                    minLength: 6
                },
                valid: false,
                touched: false
            }
        },
        isSignup: true
    };
    checkValidity(value, rules) {
        let isValid = true;
        if (!rules) {
            return true;
        }

        if (rules.required) {
            isValid = value.trim() !== '' && isValid;
        }

        if (rules.minLength) {
            isValid = value.length >= rules.minLength && isValid
        }

        if (rules.maxLength) {
            isValid = value.length <= rules.maxLength && isValid
        }

        if (rules.isEmail) {
            const pattern = /[a-z0-9!#$%&'*+/=?^_`{|}~-]+(?:\.[a-z0-9!#$%&'*+/=?^_`{|}~-]+)*@(?:[a-z0-9](?:[a-z0-9-]*[a-z0-9])?\.)+[a-z0-9](?:[a-z0-9-]*[a-z0-9])?/;
            isValid = pattern.test(value) && isValid
        }

        if (rules.isNumeric) {
            const pattern = /^\d+$/;
            isValid = pattern.test(value) && isValid
        }

        return isValid;
    }
    inputChangedHandler = (event, controlName) => {
        const updatedControls = {
            ...this.state.controls,
            [controlName]: {
                ...this.state.controls[controlName],
                value: event.target.value,
                valid: this.checkValidity(event.target.value, this.state.controls[controlName].validation),
                touched: true
            }
        };
        this.setState({controls: updatedControls});
    };
    submitHandler=(event)=>{
        event.preventDefault();
        this.props.onAuth(this.state.controls.email.value,this.state.controls.password.value,this.state.isSignup);
    };
    switchAuthHandler=()=>{
        this.setState(prevState =>{
            return {isSignup:!prevState.isSignup}
        })
    };
    componentDidMount=()=>{
        if(!this.props.buildingBurger && this.props.authRedirectPath !== '/'){
            this.props.onAuthRedirectPath('/');
        }
    };
    render(){
        let orderFormArray = [];
        for(let key in this.state.controls){
            orderFormArray.push({
                id: key,
                config: this.state.controls[key]
            })
        }
        let form = orderFormArray.map(element => (
            <Input
                key={element.id}
                elementType={element.config.elementType}
                elementConfig={element.config.elementConfig}
                value={element.config.value}
                invalid={!element.config.valid}
                validation={element.config.validation}
                touched={element.config.touched}
                change={(event)=>this.inputChangedHandler(event,element.id)}
            />
        ));

        if(this.props.loading){
            form = <Spinner/>;
        }

        let errorMessage = null;

        if(this.props.error){
            errorMessage = <p>{this.props.error.message}</p>
        }

        let redirectAuth = null;

        if(this.props.isAuthenticated){
            redirectAuth = <Redirect to={this.props.authRedirectPath}/>
        }
        return(
            <div className={classes.Auth}>
                {redirectAuth}
                {errorMessage}
                <form onSubmit={this.submitHandler}>
                    {form}
                    <Button buttonType="Success">SUBMIT</Button>
                    <Button buttonType="Danger" clicked={this.switchAuthHandler}>SWITCH TO {this.state.isSignup ? 'SIGNIN':'SIGNUP'}</Button>
                </form>
            </div>
        )
    }
}

const mapStateToProps = (state)=>{
    return{
        loading: state.auth.loading,
        error: state.auth.error,
        isAuthenticated: state.auth.token !== null,
        buildingBurger: state.burgerBuilder.building,
        authRedirectPath: state.auth.authRedirectPath
    }
};


const mapDispatchToProps = dispatch =>{
    return{
        onAuth: (email,password,isSignup) => dispatch(actions.auth(email,password,isSignup)),
        onAuthRedirectPath: (path)=>dispatch(actions.authRedirectPath(path))
    }
};


export default connect(mapStateToProps,mapDispatchToProps)(Auth);


