import React,{ Component } from 'react';
import { connect } from 'react-redux';
import { Redirect } from 'react-router-dom';

import Order from '../../components/Order/Order';
import axios from '../../axios-order';
import withErrorHandelar from "../../hoc/withErrorHandelar/withErrorHandelar";
import Spinner from '../../components/UI/Spinner/Spinner';
import * as actions from '../../store/actions';

class Orders extends Component{

    componentDidMount =()=>{
        this.props.onFetchOrders(this.props.token,this.props.userId);
    };

    render(){
        let content=<Spinner/>;
        if(!this.props.loading){
            content= this.props.orders.map(el => {
                return <Order key={el.id} ingredient={el.ingredient} price={+el.price}/>
            });
        }
        let redirectAuth = null;

        if(!this.props.isAuthenticated){
            redirectAuth = <Redirect to="/"/>
        }
        return(
            <div>
                {redirectAuth}
                {content}
            </div>
        )
    }
}

function mapStateToProps({order,auth}){
    return{
        orders: order.orders,
        loading: order.loading,
        token: auth.token,
        isAuthenticated: auth.token !== null,
        userId: auth.userId
    }
}

function mapDispatchToProps(dispatch){
    return {
        onFetchOrders: (token,userId) => dispatch(actions.fetchOrders(token,userId))
    }
}

export default connect(mapStateToProps,mapDispatchToProps)(withErrorHandelar(Orders,axios));


